*{padding: 0; margin: 0; box-sizing: border-box; user-select: none;}

body{
    background-image: linear-gradient(180deg, #8BC6EC 0%, #9599E2 100%);
}

.app{
    width: 100%;
    height: 100vh;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    color: #fff;
}

.container{
    width: 400px;
    height: 100vh;
    margin: 0 auto;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.geo{
    font-size: 2rem;
    margin: 50px;
}

.weather{
    display: flex;
    flex-direction: column;
    width: 300px;
    justify-content: center;
    align-items: center;
}

.temp{
    font-size: 4rem;
    margin: 10px;
}

.cloud{
    background: rgba(67, 106, 132, 0.3);
    padding: 8px 36px;
    border-radius: 20px;
    text-align: center;
    align-items: center;
    display: flex;
    justify-content: center;
    font-size: 1.2rem;
    margin: 10px;
}

.other{
    background: rgba(67, 106, 132, 0.3);
    width: 230px;
    border-radius: 10px;
    margin: 20px;
}

.humi, .wind{
    margin: 10px;
    font-size: 1.2rem;
}